import { ActionIcon, Navbar, Select, Space } from '@mantine/core'
import React, { useState } from 'react'
import { IoIosArrowDown, IoMdDocument, IoMdFolder } from 'react-icons/io'
import { connect } from 'react-redux'
import * as actions from '../../../actions/tree.actions'
import Node from '../../../components/Node'

const SideBar = (props: any) => {
  const [opened, setOpened] = useState(false)

  const {
    workspace,
    selectedWorkspace,
    onSelectFile,
    onChangeWorkspace,
    createNode,
    addChild,
    selecting,
  } = props

  const addNewItem = (type: string = 'folder', name: string = 'New Folder') => {
    const childId = createNode(type, name).nodeId
    if (selecting.itemType == 'folder') {
      addChild(selecting.nodeId, childId)
    } else {
      addChild(selecting.parentId, childId)
    }
  }

  const onAddNewFolder = () => {
    addNewItem()
  }

  const onAddNewFile = () => {
    addNewItem('file', 'New File')
  }

  const [currentNode, setCurrentNode] = useState<any>({})

  const getWorkspaceOptions = () => {
    if (!workspace) return []
    return workspace.map((x: any) => {
      return {
        label: x?.name,
        value: x?.key,
      }
    })
  }

  const workspaceOptions: any = getWorkspaceOptions()

  const onNodeClick = (node: any, isFile: boolean = true) => {
    if (isFile) {
      const fileContent = localStorage.getItem(node.name)
      const fileName = node.name
      const language = 'c'

      const file = {
        id: fileName,
        content: fileContent,
        name: fileName,
        language: language,
      }
      onSelectFile(file)
    }
    setCurrentNode(node)
  }

  return (
    <Navbar padding="xs">
      <div className="flex justify-between items-center">
        <label>FILE EXPLORERS</label>
        icon
      </div>

      <Space h={'md'} />
      {/* work space   */}
      <div>
        <div className="flex gap-2 items-center">
          Workspaces
          <div className="flex">
            {/* <ActionIcon>
              <IoIosAddCircleOutline />
            </ActionIcon>
            <ActionIcon>
              <IoMdCreate />
            </ActionIcon>
            <ActionIcon>
              <IoIosTrash />
            </ActionIcon> */}
          </div>
        </div>
        <Select
          onChange={onChangeWorkspace}
          value={selectedWorkspace?.key}
          data={workspaceOptions}
        />
        <Space h="sm" />

        <div className="flex gap-1 items-center">
          <IoIosArrowDown />
          <div className="flex">
            <ActionIcon onClick={onAddNewFolder}>
              <IoMdFolder />
            </ActionIcon>
            <ActionIcon onClick={onAddNewFile}>
              <IoMdDocument />
            </ActionIcon>
          </div>
        </div>
        <div className="pl-5">
          <Node id={0} />
        </div>
      </div>
    </Navbar>
  )
}

const mapStateToProps = (state: any, ownProps?: any) => ({
  ...state,
  ...ownProps,
})

export default connect(mapStateToProps, actions)(SideBar)
