import { Button, Checkbox, Divider, Navbar, Select, Space } from '@mantine/core'
import React from 'react'
import { IoIosRefresh } from 'react-icons/io'
import { connect } from 'react-redux'
import * as actions from '../../../actions/tree.actions'
import { FileService } from '../../../services/file.service'

const CompilerSettings = (props: any) => {
  const { file, addTerminalLog, currentOpeningFile } = props
  const compilerOptions = [
    {
      label: '0.8.6+commit.11564f7e',
      value: '0.8.6+commit.11564f7e',
    },
  ]

  const languageOptions = [
    {
      label: 'Solidity',
      value: 'solidity',
    },
  ]

  const evmOptions = [
    {
      label: 'london',
      value: 'london',
    },
  ]

  const onCompile = () => {
    const rawFromLocalStorage = localStorage.getItem(currentOpeningFile)
    if (!rawFromLocalStorage) {
      localStorage.setItem(currentOpeningFile, '')
    }

    const requestParams = {
      key: currentOpeningFile,
      fileContent: rawFromLocalStorage,
    }

    FileService.compileFile(requestParams).then((response: any) => {
      addTerminalLog({
        message: response.message,
        success: response.success,
      })
    })
  }

  return (
    <Navbar padding="xs">
      <div className="font-semibold">Aura IDE</div>
      <Space h="md" />
      <div className="flex flex-col gap-3">
        <div>
          <Select
            label="COMPILER"
            size="xs"
            defaultValue={compilerOptions[0].value}
            data={compilerOptions}
          />
        </div>
        <div>
          <Select
            label="LANGUAGE"
            size="xs"
            defaultValue={languageOptions[0].value}
            data={languageOptions}
          />
        </div>
        <Divider />
        <div>
          <div className="text-xs">COMPILER CONFIGURATION</div>

          <Select
            label="EVM VERSION"
            size="xs"
            defaultValue={evmOptions[0].value}
            data={evmOptions}
          />
        </div>
        <div>
          <Checkbox size="xs" label="Auto compile" />
        </div>
        <div>
          <Checkbox size="xs" label="Enable optimization" />
        </div>
        <div>
          <Checkbox size="xs" label="Hide warnings" />
        </div>
      </div>
      <Space h="md" />
      <Divider />
      <Space h="md" />
      <Button
        onClick={onCompile}
        disabled={!file?.id}
        variant="filled"
        leftIcon={
          <>
            <IoIosRefresh />
          </>
        }
        className="bg-black"
        color={'black'}
        fullWidth
      >
        Compile {file.itemName}
      </Button>
    </Navbar>
  )
}

const mapStateToProps = (state: any, ownProps: any) => ({
  ...state,
  ...ownProps,
})

export default connect(mapStateToProps, actions)(CompilerSettings)
