import axios from 'axios'
import fileCompiledResponse from './mock/compile.mock.json'

export class FileService {
  //Get list
  static compileFile(data: any) {
    // return fileCompiledResponse
    return axios({
      method: 'post',
      url: 'https://jsonplaceholder.typicode.com/users',
      data: data,
    })
  }
}
