import { Tabs } from '@mantine/core'
import Editor from '@monaco-editor/react'
import React from 'react'
import { IoIosDocument } from 'react-icons/io'

export const Mainframe = (props: any) => {
  const {
    selectedTabIndex,
    selectedTabFiles,
    onChangeEditor,
    editorContent,
  } = props
  return (
    <>
      <Tabs active={selectedTabIndex}>
        {selectedTabFiles.map((x: any, idx: any) => (
          <Tabs.Tab key={idx} label={x.itemName} icon={<IoIosDocument />} />
        ))}
        <Tabs.Tab key={1} label={'README.md'} icon={<IoIosDocument />} />
      </Tabs>
      <Editor
        defaultLanguage={'rush'}
        onChange={onChangeEditor}
        value={editorContent}
      />
    </>
  )
}
