import { ActionIcon, AppShell } from '@mantine/core'
import React, { useEffect, useState } from 'react'
import {
  IoBugOutline,
  IoDocumentsOutline,
  IoNavigateCircleOutline
} from 'react-icons/io5'
import { connect } from 'react-redux'
import * as actions from '../../../actions/tree.actions'
import { Visible } from '../../core/Visible'
import CompilerSettings from './CompilerSettings'
import { Mainframe } from './Mainframe'
import SideBar from './SideBar'
import Terminal from './Terminal'

const MainShell = (props: any) => {
  const {
    currentSelectedFiles,
    tree,
    currentOpeningFile,
    addTerminalLog,
  } = props
  const currentOpeningFileData = tree[currentOpeningFile]
  const [editorContent, setEditorContent] = useState<any>('')
  const [menu, setMenu] = useState(0)

  useEffect(() => {
    localStorage.setItem('goclamviec', JSON.stringify(props))
  }, [props])

  useEffect(() => {
    const rawFromLocalStorage = localStorage.getItem(currentOpeningFile)
    if (!rawFromLocalStorage) {
      localStorage.setItem(currentOpeningFile, '')
      setEditorContent(rawFromLocalStorage)
    } else {
      setEditorContent(rawFromLocalStorage)
    }
  }, [currentOpeningFile])

  const onChangeEditor = (content: any) => {
    if (currentOpeningFile !== -1) {
      localStorage.setItem(currentOpeningFile, content)
    }
  }

  const getListFiles = () => {
    return currentSelectedFiles.map((x: any) => {
      return tree[x]
    })
  }

  const selectedTabFiles: any[] = getListFiles()

  const selectedTabIndex =
    selectedTabFiles.findIndex((x: any) => x.id === currentOpeningFile) ?? 0

  console.log('selectedTabIndex', selectedTabIndex)
  const [tabs, setTabs] = useState<any>([
    {
      id: 0,
      content: '',
      name: 'README.md',
      language: 'c',
      active: true,
      path: 'readme_md',
    },
  ])
  const [workspace, setWorkspace] = useState<any>()

  const [selectedWorkspace, setSelectedWorkspace] = useState<any>()
  // const [selectedDirectory, setSelectedDirectory] = useState<any>()

  useEffect(() => {
    fetchWorkspace()
  }, [])

  const fetchWorkspace = () => {
    const local_WorkSpace: any = localStorage.getItem('tb_workspaces')
    setWorkspace(JSON.parse(local_WorkSpace))
  }

  useEffect(() => {
    if (workspace) {
      setSelectedWorkspace(workspace[0])
    }
  }, [workspace])

  useEffect(() => {
    fetchDirectory()
  }, [selectedWorkspace])

  const fetchDirectory = () => {
    // let local_directories: any = localStorage.getItem('tb_directories')
    // const listDirectory: any[] = JSON.parse(local_directories)
    // if (selectedWorkspace?.key) {
    // const selectedDirectory = listDirectory.find(
    // (x: any) => x.workspace === selectedWorkspace.key,
    // )
    // onInitialDirectoryTree(selectedDirectory)
    // }
  }

  const activeTabs = () => {
    const currentActive = tabs.findIndex((x: any) => x.active === true)
    if (currentActive === -1) return 0
    return currentActive
  }

  const activatedTab = activeTabs()
  const activatedItem = tabs[activatedTab]

  const onSaveDirectories = () => {}

  const handleSelectFile = (file: any) => {
    const newState = tabs.map((x: any) => {
      return {
        ...x,
        active: false,
      }
    })
    setTabs([
      ...newState,
      ...[
        {
          ...file,
          active: true,
        },
      ],
    ])
  }

  const onChangeWorkspace = (val: any) => {
    const selected = workspace.find((x: any) => x.key == val)
    setSelectedWorkspace(selected)
  }

  const menus = [
    {
      tab: 0,
      icon: IoDocumentsOutline,
    },
    {
      tab: 1,
      icon: IoBugOutline,
    },
    {
      tab: 2,
      icon: IoNavigateCircleOutline,
    },
  ]

  const onChangeMenu = (tab: any) => {
    setMenu(tab)
  }
  return (
    <>
      <AppShell
        padding="md"
        navbar={
          <div className="grid grid-cols-12">
            <div className="col-span-2 bg-black">
              <div className="flex flex-col py-5 px-3 gap-3">
                {menus.map((x: any) => {
                  const IconComponent = x.icon

                  return (
                    <ActionIcon
                      size="lg"
                      onClick={() => onChangeMenu(x.tab)}
                      className="w-full"
                    >
                      <IconComponent size={22} />
                    </ActionIcon>
                  )
                })}
              </div>
            </div>
            <div className="col-span-10">
              <Visible visible={menu === 0}>
                <SideBar
                  workspace={workspace}
                  selectedWorkspace={selectedWorkspace}
                  onChangeWorkspace={onChangeWorkspace}
                  onSelectFile={handleSelectFile}
                />
              </Visible>
              <Visible visible={menu === 1}>
                <CompilerSettings file={currentOpeningFileData} />
              </Visible>
            </div>
          </div>
        }
        classNames={{
          main: 'p-0 h-full',
        }}
      >
        <div className="h-screen flex flex-col justify-between">
          <div className="h-2/3 overflow-auto">
            <Mainframe
              selectedTabIndex={selectedTabIndex}
              selectedTabFiles={selectedTabFiles}
              onChangeEditor={onChangeEditor}
              editorContent={editorContent}
            />
          </div>
          <Terminal />
        </div>
      </AppShell>
    </>
  )
}

const mapStateToProps = (state: any) => ({
  ...state,
})

export default connect(mapStateToProps, actions)(MainShell)
